import React from 'react';
import SignupForm from '../Form/Form';
import SyncAltIcon from '@material-ui/icons/SyncAlt';
import './Aside.scss';

class Aside extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      colapsed: false
    }
  }

  showOrHide = () => {
    const aside = document.querySelector('.Aside');
    const asideHeader = document.querySelector('.Aside-header');
    aside.classList.toggle('colapsed');
    asideHeader.classList.toggle('header-colapsed');
    this.setState({
      colapsed: !this.state.colapsed
    })
  }

  render() {
    return (
      <aside className="Aside" >
        <div className="Aside-header">
          <h1 className="Aside-title">Add book</h1>
          <div
            className="Aside-switch"
            onClick={this.showOrHide}>
            <SyncAltIcon />
          </div>
        </div>
        <div>
          {this.state.colapsed === false ? <SignupForm /> : null}
        </div>
      </aside>
    );
  }
};

export default Aside;